*{
   padding: 0;
   margin: 0;
   box-sizing: border-box;
}

.main_div {
   width: 100vw;
   min-height: 100vh;
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: #6983aa;
}

.center_div {
   min-height: 70vh;
   width: 300px;
   background-color: #fff;
   border-radius: 1rem;
   box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
   text-align: center;
}

.center_div h1 {
   color: #fff;
   background: transparent;
   background-color: #8566aa;
   box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

.addBox {
   display: flex;
   justify-content: space-around;
   align-items: center;
   padding: 0 2rem;
}

.center_div input {
   width: 70%;
   margin-top: 1rem;
   margin-left: 1rem;
   text-align: center;
   outline: none;
   border: none;
   border-bottom: 3px solid #8566aa;
   font-weight: bold;
   font-size: 18px;
}

.center_div button {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 40px;
   width: 40px;
   border-radius: 50%;
   border-color: transparent;
   margin-left: 10px;
   color: #fff;
   font-size: 40px;
   outline: none;
   border: none;
   background-color: #8566aa;
   cursor: pointer;
   transition: all 0.3s ease-in;
   box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

.center_div button:hover {
   background-color: #20bf6b;
}

.center_div ol {
   margin-top: 30px;
}

.center_div li {
   text-transform: capitalize;
   font-weight: bold;
   color: #8566aa;
   text-align: left;
   list-style: none;
   padding-left: 20px;
   font-size: 20px;
   margin-bottom: 1rem;
}

.deleteBtn {
   display: inline-block;
   color: black;
   margin-right: 5px;
   transform: scale(1);
   transition: all 0.3s ease-in;
}

.deleteBtn:hover{
   color: red;
   cursor: pointer;
   transform: scale(1.5);
   text-shadow: 2px 2px 7px rgba(0,0,0,0.2);
}